@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}
body {
  background-color: aquamarine;
}

header {
  width: 100%;
  height: 3rem;
  padding: 0 2rem 0 2rem;
  background-color: rgb(206, 123, 0);
  color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .Header_name__2BZ6W {
  color: red;
  cursor: pointer;
}
header .Header_links__3KAOU {
  grid-gap: 2rem;
  gap: 2rem;
  list-style: none;
  color: black;
  text-decoration: none;
  justify-content: space-between;
}
header .Header_links__3KAOU a {
  margin-right: 3rem;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

